const Post = {
  view: ({
    attrs: {
      mdl,
      showItem,
      post: { comments_count, domain, id, points, time_ago, title, url, user },
    },
  }) => {
    return m(
      "ion-item",
      {
        id: `${id}`,
      },
      [
        m(".top", [
          m("h1.title", title),
          m(
            "code.subTitle",
            " from ",
            m(
              "a.",
              { target: "_blank", href: url, rel: "noopener" },
              `${domain}`
            )
          ),
        ]),
        m(".bottom", [
          m(".left", [
            m(
              ".top.highlight.cursor",
              {
                onclick: () => {
                  mdl.toggleUser(mdl)(user)
                },
              },
              ` by ${user}`
            ),
            m("code.bottom", `${time_ago}`),
          ]),
          m(".right", [
            m("code.highlight.top", `${points} points`),
            toComment(comments_count)(showItem)(id, title),
          ]),
        ]),
      ]
    )
  },
}

export default Post
